const util = require('../../utils/util.js')
const api = require('../../config/api.js');
const user = require('../../services/user.js')
const app = getApp()
export default {
	name: '',
	data() {
		return {
			checkout: true,
			userInfo: {}
		}
		
	},
	//初始化
	onLoad() {
	
	},
	// 页面显示
	onShow() {
		wx.hideShareMenu()
	},
	//分享
	onShareAppMessage() {
		
	},
	methods:{
		//跳转到用户协议
		servicePage() {
			wx.navigateTo({
				url: '/pages/pact/service'
			})
		},
		//调用微信登录
		wxLoginBtn: function(e) {
			var that = this;
			if (e.detail.errMsg !== 'getUserInfo:ok') {
				if (e.detail.errMsg === 'getUserInfo:fail auth deny') {
					return false;
				}
				util.showErrorToast('微信登录失败');
				return false;
			}
			util.login().then(res => {
				let userInfo = e.detail.userInfo;
				userInfo.code = res;
				console.log(userInfo)
				util.request(api.AuthLoginByWeixin,userInfo,'post').then(res => {
					if (res.code !== 0) {
						util.showErrorToast('微信登录失败');
						return false;
					}
					app.globalData.token = res.data.token;
					wx.setStorageSync('token', res.data.token);
					let pages = getCurrentPages();
					let { options } = pages.pop();
					let backUrl = '/' + options.url;
					if(wx.getStorageSync('shareUrl')) {
						backUrl = '/'+wx.getStorageSync('shareUrl')
					}
					that.getCode()
					wx.reLaunch({
						url: "" + backUrl+"",
					});
				})
			})
		},
		//获取分享code
		getCode() {
			util.request(api.getShareCode,{},'post').then(res=>{
				if(res.code == 0) {
					wx.setStorageSync('code',res.data.code)
				}
			})
		},
		checkboxChange(val) {
			if (this.checkout) {
				this.checkout = false;
			}else {
				this.checkout = true;
			}
		},
	}
}
